import React from 'react';
import shortid from 'shortid';

const Input = ({
  inputValue,
  label,
  type,
  name,
  placeholder,
  onChangeInput,
}) => {
  const id = shortid.generate();
  return (
    <>
      <label htmlFor={id}>{label}</label>
      <input
        id={id}
        value={inputValue}
        type={type}
        name={name}
        placeholder={placeholder}
        onChange={onChangeInput}
      />
    </>
  );
};

Input.defaultProps = {
  type: 'text',
};

export default Input;
